import { collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { db } from '@/lib/firebase';
import { logger } from '@/lib/logger';

export type AuditAction =
  | 'order_status_updated'
  | 'order_deleted'
  | 'product_created'
  | 'product_updated'
  | 'product_deleted'
  | 'discount_created'
  | 'discount_updated'
  | 'discount_deleted'
  | 'customer_note_saved'
  | 'inventory_adjusted'
  | 'expense_created'
  | 'expense_deleted'
  | 'bulk_delete'
  | 'message_deleted'
  | 'waitlist_exported';

interface AuditDetails {
  [key: string]: string | number | boolean | undefined;
}

/**
 * Logs an admin action to the admin_audit_log collection.
 * Non-blocking — fires and forgets so it never blocks UI actions.
 */
export function logAdminAction(
  action: AuditAction,
  details: AuditDetails,
  adminEmail?: string
): void {
  addDoc(collection(db, 'admin_audit_log'), {
    action,
    details,
    admin_email: adminEmail || 'unknown',
    timestamp: serverTimestamp(),
    created_at: new Date().toISOString(),
  }).catch((error) => {
    logger.error('Audit log write failed', { action }, error);
  });
}
